import React from "react";
import {Button, Form, Card, Spinner} from 'react-bootstrap';

export default class TradeCard extends React.Component {
    render() {
        const spinner = (sp, res) => {
            if (sp) {
                return  <div> <Spinner 
                                as="span"
                                animation="grow"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                                variant="secondary"
                            />
                            Trading... 
                        </div>
            }
            return <div className="trade-result"> {res} </div>;
        }
        
        return (
            <Card>
                <Card.Body bsPrefix={'class-body-new'}>
                    <Form onSubmit={this.props.tradeStart} className="form-trade">
                        <Form.Label>Amount trade iteration</Form.Label>
                        <Form.Control
                            type='number'
                            value={this.props.valueTrade}
                            onChange={this.props.onChangeValueTrade}
                            aria-describedby="iteration"
                            size="sm"
                        />
                        
                        <Button type="submit" size="sm" disabled={this.props.spinner}>Trade</Button>
                        {spinner(this.props.spinner, this.props.resultTrade)}
                    </Form>
                </Card.Body>
            </Card>
        );
    }
}