//округление до знака decimalPlaces после запятой
export function truncated(num, decimalPlaces) {    
    var numPowerConverter = Math.pow(10, decimalPlaces); 
    return ~~(num * numPowerConverter)/numPowerConverter;
}

//check positive number
export function positiveNumber(number) {
    return typeof number === 'string' ? +number > 0.00000000000000001 : number > 0.00000000000000001;
}

//Sord data by type [h to l]
export function sortData(data, sortType) {
    if (+sortType === 1) {
        return data.sort( (prev,next) => {
            const spreadMaxNext = next.spread[0] > next.spread[1] ? next.spread[0] : next.spread[1]
            const spreadMaxPrev = prev.spread[0] > prev.spread[1] ? prev.spread[0] : prev.spread[1]
            return spreadMaxNext - spreadMaxPrev;
        })
    }

    if (+sortType === 2) {
        return data.sort( (prev,next) => {
            const spreadMaxNext = next.spread[0] > next.spread[1] ? next.spread[0] : next.spread[1];
            const spreadMaxPrev = prev.spread[0] > prev.spread[1] ? prev.spread[0] : prev.spread[1];
            return spreadMaxPrev - spreadMaxNext;
        })
    }

    if (+sortType === 3) {
        return data.sort( (prev,next) => {
            return next.leftEx.vol24 + next.rightEx.vol24 - prev.leftEx.vol24 - prev.rightEx.vol24;
            
        })
    }

    if (+sortType === 4) {
        return data.sort( (prev,next) => {
            return prev.leftEx.vol24 + prev.rightEx.vol24 - next.leftEx.vol24 - next.rightEx.vol24;
        })
    }
    return data;
}