import React from "react";
import { LineChart, Tooltip, CartesianGrid, Line, YAxis } from 'recharts';
import { truncated } from "./additionFunc";

export default class Graphics extends React.Component {

    render() {
        const data = this.props.listSpread.map( (item, i) => {
            if (this.props.side === 'leftEx') {
                return {"count": i+1, "spread": truncated(item[0],2)}
            } else {
                return {"count": i+1, "spread": truncated(item[1],2)}
            }
        })

        return (
            <div className="grah" key={this.props.id}>
                <a className="nameGraph" href={this.props.url} target="_blank" rel="noreferrer"><b>{this.props.exchange}</b></a>
                <LineChart
                    width={250} height={90} data={data}
                    margin={{ top: 5, right: 0, bottom: 0, left: 0 }}
                >
                    <CartesianGrid vertical={false}/>
                    <YAxis type="number" domain={['auto', 'auto']} allowDataOverflow/>
                    <CartesianGrid stroke='#f5f5f5'/>
                    <Tooltip
                        wrapperStyle={{
                            borderColor: 'white',
                            boxShadow: '2px 2px 3px 0px rgb(204, 204, 204)',
                        }}
                        contentStyle={{ backgroundColor: 'rgba(255, 255, 255, 0.8)' }}
                        labelStyle={{ fontWeight: 'bold', color: '#666666' }}
                    />
                    <Line dataKey="spread" stroke="#ff7300" dot={false} />

            
                </LineChart>
            </div>
        );
    }
}